import Vue from 'vue'
import Router from 'vue-router'
import Utils from './utils';

const Login = () => import(/* webpackChunkName: "Login" */'./views/Login');

const EmployeeLayout = () => import(/* webpackChunkName: "Employee" */'./views/Employee/EmployeeLayout');
const EmployeeTimesheetList = () => import(/* webpackChunkName: "Employee" */'./views/Employee/EmployeeTimesheetList');
const EmployeeTimesheetDetail = () => import(/* webpackChunkName: "Employee" */'./views/Employee/EmployeeTimesheetDetail');

const JobLayout = () => import(/* webpackChunkName: "Job" */'./views/Job/JobLayout');
const JobTimesheetList = () => import(/* webpackChunkName: "Job" */'./views/Job/JobTimesheetList');
const JobTimesheetDetail = () => import(/* webpackChunkName: "Job" */'./views/Job/JobTimesheetDetail');

const RegionLayout = () => import(/* webpackChunkName: "Region" */'./views/Region/RegionLayout');
const RegionTimesheetList = () => import(/* webpackChunkName: "Region" */'./views/Region/RegionTimesheetList');
const RegionTimesheetDetail = () => import(/* webpackChunkName: "Region" */'./views/Region/RegionTimesheetDetail');

const EquipmentRepairLayout = () => import(/* webpackChunkName: "EquipmentRepair" */'./views/EquipmentRepair/EquipmentRepairLayout');
const EquipmentRepairList = () => import(/* webpackChunkName: "EquipmentRepair" */'./views/EquipmentRepair/EquipmentRepairList');
const EquipmentRepairDetail = () => import(/* webpackChunkName: "EquipmentRepair" */'./views/EquipmentRepair/EquipmentRepairDetail');

Vue.use(Router)

const router = new Router({
  routes:[
    {
      path :'/Login',
      name :'Login',
      component: Login
    },
    {
      path: '',
      name: 'EmployeeLayout',
      component: EmployeeLayout,
      redirect: '/EmployeeTimesheetList',
      children:[
        {
          path :'/EmployeeTimesheetList',
          name :'EmployeeTimesheetList',
          component: EmployeeTimesheetList
        },
        { 
          path: '/EmployeeTimesheetDetail/:PK', 
          name: 'EmployeeTimesheetDetail',
          component: EmployeeTimesheetDetail
        }
      ]
    },
    {
      path: '',
      name: 'JobLayout',
      component: JobLayout,
      redirect: '/JobTimesheetList',
      children:[
        {
          path :'/JobTimesheetList',
          name :'JobTimesheetList',
          component: JobTimesheetList
        },
        { 
          path: '/JobTimesheetDetail/:PK/:JobNumber?/:DateTime?', 
          name: 'JobTimesheetDetail',
          component: JobTimesheetDetail
        }
      ]
    },
    {
      path: '',
      name: 'RegionLayout',
      component: RegionLayout,
      redirect: '/RegionTimesheetList',
      children:[
        {
          path :'/RegionTimesheetList',
          name :'RegionTimesheetList',
          component: RegionTimesheetList
        },
        { 
          path: '/RegionTimesheetDetail/:PK', 
          name: 'RegionTimesheetDetail',
          component: RegionTimesheetDetail
        }
      ]
    },
    {
      path: '',
      name: 'EquipmentRepairLayout',
      component: EquipmentRepairLayout,
      redirect: '/EquipmentRepairList',
      children:[
        {
          path :'/EquipmentRepairList',
          name :'EquipmentRepairList',
          component: EquipmentRepairList
        },
        { 
          path: '/EquipmentRepairDetail/:PK', 
          name: 'EquipmentRepairDetail',
          component: EquipmentRepairDetail
        }
      ]
    }
  ]
});

router.beforeEach((to, from, next)=>{
      if (to.path == '/login') {
        Utils.clearSessionStorage();
      }
      
      if (!Utils.isUserTokenExist() && to.path != '/login') {
        next({ path: '/login' });
      } else {
        next();
      } 
});

export default router;