import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    employeeTimeSheetList: [],
    jobTimeSheetList: [],
    regionTimeSheetList: [],
    equipmentRepairList: [],
    photo: ''
  },
  mutations: {
    setEmployeeTimeSheetList(state, data){
			state.employeeTimeSheetList = data;
    },
    setJobTimeSheetList(state, data){
			state.jobTimeSheetList = data;
    },
    setRegionTimeSheetList(state, data){
			state.regionTimeSheetList = data;
    },
    setEquipmentRepairList(state, data){
			state.equipmentRepairList = data;
    },
    setPhoto(state, data){
			state.photo = data;
    }
  },
  actions: {

  }
})
