import { Indicator, Toast, MessageBox  } from 'mint-ui';

export default
{
	install(Vue, options)
	{
		Vue.filter('formatDate', (value, fmt)=>{
			let date = new Date(Date.parse(value));
			let o = { 
				'M+' : date.getMonth()+1,                 //Month
				'd+' : date.getDate(),                    //Day
				'h+' : date.getHours(),                   //Hour 
				'm+' : date.getMinutes(),                 //Minute
				's+' : date.getSeconds(),                 //Second
				'q+' : Math.floor((date.getMonth()+3)/3), //Season
				'S'  : date.getMilliseconds()             //MilliSecond
			}; 

			if(/(y+)/.test(fmt)) 
			{
				fmt=fmt.replace(RegExp.$1, (date.getFullYear()+'').substr(4 - RegExp.$1.length)); 
			}

			for(let k in o) 
			{
				if(new RegExp('('+ k +')').test(fmt))
				{
					fmt = fmt.replace(RegExp.$1, (RegExp.$1.length==1) ? (o[k]) : (('00'+ o[k]).substr((''+ o[k]).length)));
				}
			}
			return fmt; 	
		});

		Vue.mixin({
			methods:{
				showNetworkError()
				{
					Toast({message: 'The Web Server may be too busy, please try again at a later time.', type: 'error'});
				}
			}
		});

		Vue.prototype.$formatDate = (date, fmt) => {
			let o = { 
				'M+' : date.getMonth()+1,                 //Month
				'd+' : date.getDate(),                    //Day
				'h+' : date.getHours(),                   //Hour 
				'm+' : date.getMinutes(),                 //Minute
				's+' : date.getSeconds(),                 //Second
				'q+' : Math.floor((date.getMonth()+3)/3), //Season
				'S'  : date.getMilliseconds()             //MilliSecond
			}; 

			if(/(y+)/.test(fmt)) 
			{
				fmt=fmt.replace(RegExp.$1, (date.getFullYear()+'').substr(4 - RegExp.$1.length)); 
			}

			for(let k in o) 
			{
				if(new RegExp('('+ k +')').test(fmt))
				{
					fmt = fmt.replace(RegExp.$1, (RegExp.$1.length==1) ? (o[k]) : (('00'+ o[k]).substr((''+ o[k]).length)));
				}
			}
			return fmt; 	
		};

		Vue.prototype.$message = (data) => {
			Toast({message: data.message, duration: 3000});
		};

		Vue.prototype.$alert = (message, title) => {
			MessageBox.alert(message, title, {confirmButtonText: 'OK'});
		};

		Vue.prototype.$confirm = (message, title) => {
			return MessageBox.confirm(message, title, {confirmButtonText: 'OK', cancelButtonText: 'Cancel'});
		};

		Vue.prototype.$prompt = (title, input) => {
			if(input)
			{
				return MessageBox.prompt(' ', title, {inputValue: input, confirmButtonText: 'OK', cancelButtonText: 'Cancel'});
			}
			else
			{
				return MessageBox.prompt(' ', title, {confirmButtonText: 'OK', cancelButtonText: 'Cancel'});
			}
		};

		Vue.prototype.$showLoading = () => {
			Indicator.open();
		};

		Vue.prototype.$closeLoading = () => {
			Indicator.close();
		};

		Vue.prototype.$takeCameraPicture = (onSuccess) => {
			try 
			{
				if (navigator.camera) 
				{
					navigator.camera.getPicture(
						onSuccess, 
						(error)=>{console.log(error);}, 
						{
							quality: 45,
							destinationType: 0,//Camera.DestinationType.DATA_URL,
							sourceType: 1,//Camera.PictureSourceType.CAMERA,
							allowEdit: true,
							encodingType: 0,//Camera.EncodingType.JPEG,
							targetWidth: 250,
							targetHeight: 250
						}
					);
				} 
				else 
				{
					let input = document.createElement("input"); 
					input.setAttribute("type", "file"); 
					input.onchange = (event)=>{
						try 
						{
							var file = event.currentTarget.files[0];
							if (!file)
								return;
					
							var reader = new FileReader();
							reader.onload = function (e) 
							{
								var dataURL = e.target.result;
								var canvas = document.createElement('canvas');
					
								var img = new Image();
								img.onload = function () 
								{
									var square = 250;
									canvas.width = square;
									canvas.height = square;
									var context = canvas.getContext('2d');
									context.clearRect(0, 0, square, square);
					
									var imageWidth;
									var imageHeight;
									var offsetX = 0;
									var offsetY = 0;
									if (this.width > this.height) 
									{
										imageWidth = Math.round(square * this.width / this.height);
										imageHeight = square;
										offsetX = -Math.round((imageWidth - square) / 2);
									} 
									else 
									{
										imageHeight = Math.round(square * this.height / this.width);
										imageWidth = square;
										offsetY = -Math.round((imageHeight - square) / 2);
									}
									context.drawImage(this, offsetX, offsetY, imageWidth, imageHeight);
									var base64 = canvas.toDataURL("image/jpeg", 0.5);
									var imgData = base64.substr(23);
									onSuccess(imgData);
								};
								img.src = dataURL;
							};
							reader.readAsDataURL(file);
						}
						catch (err) 
						{
							console.log(err);
						}
					};
					input.click();
				}
			}
			catch (err) 
			{
				console.log(err);
			}
		};
	}
}
