export default class Utils {
  static getAppName() {
    return "BOETA_Mobile_RC_Hub";
  }

  static getBaseUrl() {
    //return 'http://localhost:12178/api/';
    return "https://BOETAapitest.azurewebsites.net/api/";
  }

  static setJobNumber(jobNumber) {
    localStorage.setItem(Utils.getAppName() + "_remeberJob", jobNumber);
  }

  static getJobNumber() {
    return localStorage.getItem(Utils.getAppName() + "_remeberJob") || "";
  }

  static setRemeberMe(userId, password, ticks) {
    let user = {
      UserId: userId,
      Password: password,
      Ticks: ticks
    };
    localStorage.setItem(
      Utils.getAppName() + "_remeberMe",
      JSON.stringify(user)
    );
  }

  static getRemeberMe() {
    let userString =
      localStorage.getItem(Utils.getAppName() + "_remeberMe") || "";
    if (userString.length == 0) {
      return {
        UserId: "",
        Password: "",
        Ticks: ""
      };
    }

    let user = JSON.parse(userString);
    return {
      UserId: user.UserId,
      Password: user.Password,
      Ticks: user.Ticks
    };
  }

  static clearLocalStorage() {
    localStorage.removeItem(Utils.getAppName() + "_remeberJob");
    localStorage.removeItem(Utils.getAppName() + "_remeberMe");
  }

  static setUserSettings(settings) {
    sessionStorage.setItem(
      Utils.getAppName() + "_userSettings",
      JSON.stringify(settings)
    );
  }

  static getUserSettings() {
    let settingsString =
      sessionStorage.getItem(Utils.getAppName() + "_userSettings") || "";
    return JSON.parse(settingsString);
  }

  static getUserToken() {
    let tokenString =
      sessionStorage.getItem(Utils.getAppName() + "_userToken") || "";
    let token = JSON.parse(tokenString);

    return {
      UserId: token.UserId,
      Password: token.Password,
      Ticks: token.Ticks,
      Secret: token.Secret,
      EncryptPassword: true,
      TokenID: "70d3fa69-4d3d-4cb7-882e-f3e97fa0352d",
      Settings: "",
      CompressType: ""
    };
  }

  static setUserToken(userId, passwordHash, ticks, secret) {
    let token = {
      UserId: userId,
      Password: passwordHash,
      Ticks: ticks,
      Secret: secret
    };
    sessionStorage.setItem(
      Utils.getAppName() + "_userToken",
      JSON.stringify(token)
    );
  }

  static clearSessionStorage() {
    sessionStorage.removeItem(Utils.getAppName() + "_userSettings");
    sessionStorage.removeItem(Utils.getAppName() + "_userToken");
  }

  static logout() {
    Utils.clearLocalStorage();
    Utils.clearSessionStorage();
  }

  static isUserTokenExist() {
    let userId =
      sessionStorage.getItem(Utils.getAppName() + "_userToken") || "";
    return userId.length > 0;
  }

  static createUserToken(userId, passwordHash, ticks) {
    return {
      UserId: userId,
      Password: passwordHash,
      Ticks: ticks,
      EncryptPassword: true,
      TokenID: "70d3fa69-4d3d-4cb7-882e-f3e97fa0352d",
      Settings: "",
      CompressType: ""
    };
  }

  static getTicks() {
    let epochTicks = 621355968000000000;
    let ticksPerMillisecond = 10000;
    return epochTicks + new Date().getTime() * ticksPerMillisecond;
  }

  static encryptPassword(ticks, value) {
    let array = new Array();
    for (let i = 0; i < value.length; i++) {
      if (i < ticks.length) {
        let ret = value.charCodeAt(i) + ticks.charCodeAt(i);
        array.push(ret.toString());
      } else {
        let ret = value.charCodeAt(i) + 18;
        array.push(ret.toString());
      }
    }

    return array.join(" ") + " ";
  }

  static base64ToByteArray(base64) {
    let binary = atob(base64);
    let array = new Uint8Array(binary.length);
    for (var i = 0; i < binary.length; i++) {
      array[i] = binary.charCodeAt(i);
    }
    return array;
  }
}
